import Vue from "vue";
import Vuex from "vuex";
import {getMassageData} from "../service/api";


Vue.use(Vuex);

export default new Vuex.Store({
    state:{
        /*留言数据*/
        comments:[]
    },
    mutations:{
        setComments(state,comments){
            state.comments = comments;
        }
    },
    actions:{
        getMassage({commit}){
            getMassageData()
                .then(response => {
                    commit("setComments",response.data) ;
                })
                .catch(error => {
                    console.log(error);
                });
        },
    },
    getters:{
        messageCount(state){
            return state.comments.length;
        }
    }
})