import ajax from "./ajax";

const BASE_URL = "https://www.qiflyb.cn:3000";
// const BASE_URL = "http://127.0.0.1:8081";
//请求首页留言板数据
export const getMassageData = () => ajax(BASE_URL + "/api/pepolmessage");
export const postSenMag = (parmas) => ajax(BASE_URL + "/api/savemassage", parmas, "POST");

//请求音乐数据
export const getMusicInfo = () => ajax(BASE_URL + "/api/musicinfo");

//请求照片数据
export const getImageInfo = () => ajax(BASE_URL + "/api/imageinfo");
export const getHeaderImg = () => ajax(BASE_URL + "/api/headerImg");

//请求文章数据
export const getArticleTitle = () => ajax(BASE_URL + "/api/articleinfo");
export const getArticle = (parmas) => ajax(BASE_URL + "/api/webarticle", parmas);