import Vue from "vue";
import Router from "vue-router";

//引入一级组件
const Personal = () => import("../views/personal/Personal");
const Home = () => import("../views/home/Home");
const Imageborad = () => import("../views/imageborad/Imageborad");

//引入二级组件
const Article = () => import("../views/home/components/article/Article");
const Message = () => import("../views/home/Message");
const Articles = () => import("../views/articles/Articles");

Vue.use(Router);

export default new Router({
    // mode: history,
    routes: [
        {
            path: "/",
            name: "home",
            component: Home,
            children: [
                {path: "/", redirect: "/article"},
                {path: "article", name: "article", component: Article},
                {path: "message", name: "message", component: Message},
            ]
        },
        {path: "/articles/:id", name: "articles", component: Articles, props: true},
        {path: "/imageborad", name: "imageborad", component: Imageborad},
    ]
});